import React, { Fragment } from 'react'
import { Grid, Button, Typography } from "@material-ui/core"
import { withRouter } from "react-router-dom"
import { Marker, AFrameRenderer } from "react-web-ar"

import { render } from "react-dom"

class FieldPage extends React.Component {
	constructor() {
		super()
		this.state = {

		}
	}

	render() {
		return (
			<div>
				<AFrameRenderer arToolKit={{ sourceType: 'webcam' }}>
					<Marker parameters={{ preset: 'hiro' }}>

						<a-assets-item img id="appTicket" src="https://raw.githubusercontent.com/lbelfield/augmented-reality/master/src/components/railcard/app-ticket.png" />
						<a-assets-item img id="journeyDetails" src="https://raw.githubusercontent.com/lbelfield/augmented-reality/master/src/components/railcard/journey-details.png" />
						<a-assets-item img id="informationIcon" src="https://raw.githubusercontent.com/lbelfield/augmented-reality/master/src/components/railcard/information-icon.png" />
						<a-assets-item img id="trainIcon" src="https://raw.githubusercontent.com/lbelfield/augmented-reality/master/src/components/railcard/train-icon.png" />
						<a-assets-item img id="userProfile" src="https://raw.githubusercontent.com/lbelfield/augmented-reality/master/src/components/railcard/user-profile.png" />

						<a-entity camera look-controls mouse-cursor>
							<a-entity
								id="ticket"
								ticket-listener
								cursor="fuse: true; fuseTimeout: 500"
								position="1 0 0"
								geometry="primitive: box; height: 0.1; depth: 1.5; rotation: 90 0 0;"
								material="shader: flat;"
								visible="false">
								<a-animation attribute="position"
									dur="3000"
									begin="ticketAnimation"
									from="0 0 0"
									to="1 0 0" />
							</a-entity>

							<a-entity
								id="journey-details"
								journey-details-listener
								cursor="fuse: true; fuseTimeout: 500"
								position="2 0 0"
								geometry="primitive: box; height: 0.1; depth: 1.5; rotation: 90 0 0;"
								material="shader: flat; src: #journeyDetails"
								visible="false">

								<a-animation attribute="position"
									dur="3000"
									begin="journeyDetailsAnimation"
									from="1 0.1 0"
									to="2 0.01 0" />

							</a-entity>

							<a-entity
								id="cylinder-info-icon"
								cylinder-info-icon-listener
								cursor="fuse: true; fuseTimeout: 500"
								position="-0.25 0.1 0.5"
								geometry="primitive: cylinder; height: 0.1; radius: 0.15"
								material="shader: flat; src: #informationIcon" />

							<a-entity
								id="cylinder-train-icon"
								cylinder-train-icon-listener
								cursor="fuse: true; fuseTimeout: 500"
								position="0.25 0.1 0.5"
								geometry="primitive: cylinder; height: 0.1; radius: 0.15"
								material="shader: flat; src: #trainIcon" />

						</a-entity>

					</Marker>
				</AFrameRenderer>
			</div>
		)
		// return (<div></div>)
	}
}


export default FieldPage

